export default {
	data() {
		return {
		globalUser:''	
		}
	},
	onShow() {
		var globalUser = this.getGlobalUser("globalUser");
		this.globalUser = globalUser;
	},
	methods: {
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: '清理缓存成功',
				mask:false,
				duration:1500
			})
		},
		logout() {
			uni.removeStorageSync("globalUser")
			uni.switchTab({
				url:"../me/me"
			})
		}
	}
}
